.projects-container {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.projects-container h1 {
  text-align: center;
  color: #00b894;
  margin-bottom: 3rem;
  font-size: 2.5rem;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.project-card {
  background: var(--bg-white);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 600px;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 1.5rem;
}

.project-card h3 {
  color: #2d3436;
  margin-bottom: 0.5rem;
}

.project-card p {
  color: #636e72;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.tech-stack {
  margin-top: auto;
  padding-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: #f0f0f0;
  padding: 0.2rem 0.6rem;
  border-radius: 15px;
  font-size: 0.8rem;
  color: var(--text-secondary);
}

.project-links {
  margin-top: 1rem;
  text-align: center;
}

.github-link {
  display: inline-block;
  padding: 0.6rem 1.2rem;
  background-color: #00b894;
  color: white !important;
  text-decoration: none;
  border-radius: 25px;
  transition: all 0.3s ease;
  border: none;
  font-size: 0.9rem;
}

.github-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 184, 148, 0.2);
  background-color: #00a382;
  color: white !important;
}

.project-indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 2rem;
}

.indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ddd;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background-color: #00b894;
  transform: scale(1.2);
}

@media (max-width: 1200px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  }
}

@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }

  .project-card {
    max-height: 500px;
  }

  .content-wrapper {
    max-height: 150px;
  }

  .project-card {
    padding: 1rem;
    font-size: 0.85rem;
  }

  .project-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .project-date {
    font-size: 0.75rem;
    padding: 0.15rem 0.5rem;
  }

  .project-card h4 {
    font-size: 1.1rem;
  }

  .tech-stack {
    gap: 0.3rem;
    margin: 0.8rem 0;
  }

  .tech-tag {
    padding: 0.15rem 0.5rem;
    font-size: 0.75rem;
  }

  .project-links {
    padding-top: 1rem;
  }

  .github-link {
    padding: 0.5rem 1rem;
    font-size: 0.85rem;
    width: 100%;
  }

  .nav-button {
    padding: 0.3rem;
  }

  .project-description-list,
  .project-details-list {
    padding-left: 1rem;
    font-size: 0.8rem;
  }

  .project-description-list li,
  .project-details-list li {
    margin-bottom: 0.25rem;
  }
}

@media (max-width: 480px) {
  .projects-grid {
    padding: 0 0.5rem;
  }

  .project-card {
    padding: 0.8rem;
  }

  .content-wrapper {
    max-height: 120px;
  }
}

@media (hover: none) {
  .content-wrapper {
    -webkit-overflow-scrolling: touch;
  }

  .content-wrapper::-webkit-scrollbar {
    width: 4px;
  }
}

.project-content {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1rem 0;
  flex: 1;
  min-height: 0;
}

.content-wrapper {
  flex: 1;
  overflow-y: auto;
  max-height: 200px;
  padding: 0 0.5rem;
  min-height: 0;
}

.nav-button {
  background: none;
  border: none;
  color: #00b894;
  cursor: pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
  flex-shrink: 0;
}

.nav-button:hover {
  transform: scale(1.1);
}

.content-view,
.achievements-list {
  display: none;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.project-date {
  font-size: 0.8rem;
  color: #00b894;
  padding: 0.2rem 0.6rem;
  background-color: rgba(0, 184, 148, 0.1);
  border-radius: 15px;
}

.project-card h4 {
  font-size: 1.2rem;
  color: var(--text-primary);
  margin: 0;
}

.project-description-list,
.project-details-list {
  list-style-type: disc;
  padding-left: 1.2rem;
  margin-bottom: 0.8rem;
  color: var(--text-secondary);
  font-size: 0.85rem;
}

.project-description-list li,
.project-details-list li {
  margin-bottom: 0.3rem;
  line-height: 1.4;
}

.project-content::-webkit-scrollbar {
  width: 6px;
}

.project-content::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.project-content::-webkit-scrollbar-thumb {
  background: #00b894;
  border-radius: 3px;
}

.project-details-list {
  margin: 0;
  padding-left: 1.2rem;
}