.contact-container {
  padding: 2rem;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.contact-container h1 {
  color: #00b894;
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

.contact-intro {
  font-size: 1.2rem;
  color: #636e72;
  margin-bottom: 3rem;
  line-height: 1.6;
}

.contact-methods {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.contact-method {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.contact-method:hover {
  transform: translateY(-5px);
}

.contact-method h3 {
  color: #00b894;
  margin-bottom: 1rem;
}

.contact-method a {
  color: #2d3436;
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact-method a:hover {
  color: #00b894;
}

@media (max-width: 768px) {
  .contact-section {
    padding: 1rem;
  }

  .contact-container {
    padding: 1.5rem;
  }

  .contact-container h1 {
    font-size: 1.75rem;
  }

  .contact-intro {
    font-size: 0.9rem;
    margin-bottom: 1.5rem;
  }

  .contact-methods {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .contact-method {
    padding: 1rem;
  }

  .contact-method h3 {
    font-size: 1.1rem;
  }

  .contact-method p,
  .contact-method a {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .contact-section {
    padding: 0.8rem;
  }

  .contact-container {
    padding: 1rem;
  }

  .contact-method {
    padding: 0.8rem;
  }
}