.experience-container {
  padding: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

.experience-container h1 {
  text-align: center;
  color: #00b894;
  margin-bottom: 3rem;
  font-size: 2.5rem;
}

.timeline {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.experience-card {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.experience-card:hover {
  transform: translateY(-5px);
}

.experience-header h4 {
  color: #00b894;
  margin: 0;
  font-size: 1.5rem;
}

.experience-header h5 {
  color: #2d3436;
  margin: 0.5rem 0;
}

.period {
  color: #636e72;
  font-size: 0.9rem;
}

.responsibilities {
  list-style-type: none;
  padding-left: 0;
  margin: 1.5rem 0;
}

.responsibilities li {
  color: #2d3436;
  margin-bottom: 0.8rem;
  padding-left: 1.5rem;
  position: relative;
  line-height: 1.6;
}

.responsibilities li::before {
  content: "▹";
  position: absolute;
  left: 0;
  color: #00b894;
}

.tech-used {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-badge {
  background: #f8f9fa;
  color: #2d3436;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  border: 1px solid #e9ecef;
}
.company-info {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.company-logo {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

.company-info div {
  flex: 1;
}

  
.experience-container h3 {
  text-align: center;
  color: #00b894;
  margin-bottom: 2rem;
  font-size: 2rem;
}

@media (max-width: 768px) {
  .experience-section {
    padding: 1rem;
  }

  .experience-card {
    padding: 1rem;
    margin-bottom: 1.5rem;
  }

  .company-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.8rem;
  }

  .company-logo {
    width: 40px;
    height: 40px;
  }

  .company-info h3 {
    font-size: 1.1rem;
  }

  .company-info h4 {
    font-size: 1rem;
  }

  .period {
    font-size: 0.8rem;
  }

  .responsibilities li {
    font-size: 0.85rem;
    margin-bottom: 0.5rem;
    padding-left: 1rem;
  }

  .tech-badge {
    font-size: 0.75rem;
    padding: 0.2rem 0.6rem;
  }
}

@media (max-width: 480px) {
  .experience-section {
    padding: 0.8rem;
  }

  .experience-card {
    padding: 0.8rem;
  }
}